import { CommonHelper } from './../helpers/—Åommon-helper';
export class InputData {
  public currMoney: number = 1000000;      // —Å–µ–π—á–∞—Å –≤ –Ω–∞–ª–∏—á–∏–∏ –¥–µ–Ω–µ–≥
  public canPayM: { [month: number]: number } = { 0: 70000 };        // –º–æ–≥—É –ø–ª–∞—Ç–∏—Ç—å, –≤ –º–µ—Å

  public mortRateY: number = 3;      // —Å—Ç–∞–≤–∫–∞ –ø–æ –∏–ø–æ—Ç–µ–∫–µ, % –≤ –≥–æ–¥
  public investRateY: number = 3;    // —Å—Ç–∞–≤–∫–∞ –ø–æ –≤–∫–ª–∞–¥—É, % –≤ –≥–æ–¥

  public propInflationY: number = 2;     // —É–¥–æ—Ä–æ–∂–∞–Ω–∏–µ –∫–≤–∞—Ä—Ç–∏—Ä—ã, % –≤ –≥–æ–¥
  public rentInflationY: number = 2;     // —É–¥–æ—Ä–æ–∂–∞–Ω–∏–µ –∞—Ä–µ–Ω–¥—ã, % –≤ –≥–æ–¥
  public comFeesInflationY: number = 3;  // —É–¥–æ—Ä–æ–∂–∞–Ω–∏–µ –∫–æ–º–º—É–Ω–∞–ª–∫–∏, % –≤ –≥–æ–¥
  public payInflationY: number = 0;      // —É–≤–µ–ª–∏—á–µ–Ω–∏–µ –ø–ª–∞—Ç–µ–∂–∞, % –≤ –≥–æ–¥

  public flatPrice = 7_000_000 //—Ü–µ–Ω–∞ –∫–≤–∞—Ä—Ç–∏—Ä—ã

  public get propPriceIn10Years(): number { return CommonHelper.inflateYearly(this.flatPrice, this.propInflationY, 120); }
  public get canPayIn10Years(): number { return CommonHelper.inflateYearly(this.canPayM[0], this.payInflationY, 120); }
}
