import { CommonHelper } from '../helpers/—Åommon-helper';

export class MortSettings {
  propPrice: number = 7_000_000;   // —Ü–µ–Ω–∞ –∫–≤–∞—Ä—Ç–∏—Ä—ã
  mortRate: number = 6;            // —Å—Ç–∞–≤–∫–∞ –ø–æ –∏–ø–æ—Ç–µ–∫–µ
  comFeesM: number = 3500;         // –∫–æ–º–º—É–Ω–∞–ª—å–Ω—ã–µ –ø–ª–∞—Ç–µ–∂–∏ –±–µ–∑ —Å—á–µ—Ç—á–∏–∫–æ–≤ (—Ñ–∏–∫—Å), –≤ –º–µ—Å
  propTaxY: number = 30_000;       // –Ω–∞–ª–æ–≥, —Å—Ç—Ä–∞—Ö–æ–≤–∫–∞, –ø—Ä–æ—á–∏–µ –ø–ª–∞—Ç–µ–∂–∏ –≤–ª–∞–¥–µ–Ω–∏—è –∫–≤–∞—Ä—Ç–∏—Ä–æ–π, –≤ –≥–æ–¥
  comRateY: number = 2;            // —É–¥–æ—Ä–æ–∂–∞–Ω–∏–µ –∫–æ–º–º—É–Ω–∞–ª–∫–∏, % –≤ –≥–æ–¥

  public get comFeesIn10Years() { return CommonHelper.inflateYearly(this.comFeesM, this.comRateY, 120); }
}